// 
const preview = (imgList, index) => {
	return new Promise((resolve, reject) => {
		uni.previewImage({
			current: index,
			urls: imgList,
			longPressActions: {
				itemList: ["", "", ""]
			}
		}).then(res => {
			resolve(res);
		}).catch(err => {
			reject(err);
		})
	})
};

export {
	preview
}